/**
 * extracts the attributes and passes them to a template literal that builds the
 * Cubic Bezier curve representation.
 *
 * @param {*} cubicBezierCurve
 */
export const pathFromBezierCurve = cubicBezierCurve => {
    const {
        initialAxis,
        initialControlPoint,
        endingControlPoint,
        endingAxis,
    } = cubicBezierCurve;
    return `
        M${initialAxis.x} ${initialAxis.y}
        c ${initialControlPoint.x} ${initialControlPoint.y}
        ${endingControlPoint.x} ${endingControlPoint.y}
        ${endingAxis.x} ${endingAxis.y}
    `;
};

// https://math.stackexchange.com/questions/714378/find-the-angle-that-creating-with-y-axis-in-degrees
export const radiansToDegrees = radians => ((radians * 180) / Math.PI);
export const calculateAngle = (x1, y1, x2, y2) => {
    if (x2 >= 0 && y2 >= 0) {
        return 90;
    } else if (x2 < 0 && y2 >= 0) {
        return -90;
    }
    const dividend = x2 - x1;
    const divisor = y2 - y1;
    const quotient = dividend / divisor;
    /**
     * The atan function, provided by the Math JavaScript object, returns results in radians
     */
    return radiansToDegrees(Math.atan(quotient)) * -1;
};
/**
 * mouse position on auto-scaling canvas
 * https://stackoverflow.com/a/10298843/1232793
 *
 * @param {*} event
 */
export const getCanvasPosition = (event) => {
    const svg = document.getElementById('aliens-go-home-canvas');
    const point = svg.createSVGPoint();
    point.x = event.clientX;
    point.y = event.clientY;
    const { x, y } = point.matrixTransform(svg.getScreenCTM().inverse());
    return {x, y};
};



